import React, { ChangeEvent, useEffect, useState } from 'react'
import './style.css';
import { useCookies } from 'react-cookie';
import { ACCESS_TOKEN } from 'src/constants';
import { PatchToolRequestDto, PostToolRequestDto } from 'src/apis/dto/request/tool';
import { deleteToolRequest, getToolListRequest, getToolRequest, patchToolRequest, postToolRequest } from 'src/apis';
import { ResponseDto } from 'src/apis/dto/response';
import { Tool } from 'src/types';
import { GetToolListResponseDto, GetToolResponseDto } from 'src/apis/dto/response/tool';
import { usePagenation } from 'src/hooks';
import Pagenation from 'src/components/Pagenation';

// interface:    Properties //
interface PostBoxProps {
  // Props         
    unShow: () => void;
    getToolList: () => void;
}

// component:    //
function PostBox({ unShow, getToolList }: PostBoxProps) {

    // state: cookie  //
    const [cookies] = useCookies();

    // state:    //
    const [name, setName] = useState<string>('');
    const [purpose, setPurpose] = useState<string>('');
    const [count, setCount] = useState<string>('');

    // function: post tool response   //
    const postToolResponse = (responseBody: ResponseDto | null) => {
        const message = 
            !responseBody ? '  .' :
            responseBody.code === 'VF' ? ' .' :
            responseBody.code === 'AF' ? ' .' :
            responseBody.code === 'DBE' ? '  .' : '';
        
            //  null  .  
        const isSuccessed = responseBody !== null && responseBody.code === 'SU';
        //       
        if (!isSuccessed) {
            alert(message);
            return;
        }
        
        getToolList();
        unShow();
    };

    // event handler:       //
    const onNameChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        //      
        setName(value);
    };

    // event handler:      //
    const onPurposeChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        setPurpose(value);
    };

    // event handler:      //
    const onCountChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        //    
        const regexp = /^[0-9]*$/;
        //   
        const isNumber = regexp.test(value);
        if (!isNumber) return;
        setCount(value);
    };


    // event handler:       //
    const onPostButtonClickHandler = () => {
        const accessToken = cookies[ACCESS_TOKEN];
        if (!accessToken) return;

        //  , ,       
        if (!name || !purpose || !count) {
            alert(' .');
            return;
        }
        const requestBody: PostToolRequestDto = {
            //        
            name, purpose, count: Number(count)
        };

        postToolRequest(requestBody, accessToken).then(postToolResponse);
    };

    // render:     //
    return (
        <div className='post-patch-box'>
            <div className='post-patch-input-container'>
                <div className='input-box'>
                    <div className='input-label'></div>
                    <input className='input' value={name} placeholder=' ' onChange={onNameChangeHandler} />
                </div>
                <div className='input-box' style={{ flex: 1 }}>
                    <div className='input-label'></div>
                    <input className='input' value={purpose} placeholder=' ' onChange={onPurposeChangeHandler} />
                </div>
                <div className='input-box'>
                    <div className='input-label'></div>
                    <input className='input' value={count} placeholder=' ' onChange={onCountChangeHandler} />
                </div>
            </div>
            <div className='button primary' onClick={onPostButtonClickHandler}></div>
            <div className='button disable' onClick={unShow}></div>
        </div>
    )

}

// interface:    Properties //
interface PatchBoxProps {
    toolNumber: number;
    unShow: () => void;
    getToolLIst: () => void;
}

// component:    //
function PatchBox({ toolNumber, unShow, getToolLIst }: PatchBoxProps) {

    // state: cookie  //
    const [cookies] = useCookies();

    // state:    //
    const [name, setName] = useState<string>('');
    const [purpose, setPurpose] = useState<string>('');
    const [count, setCount] = useState<string>('');

    // function: get tool response   //
    const getToolResponse = (responseBody: GetToolResponseDto | ResponseDto | null) => {
        const message =
            !responseBody ? '  .' :
            responseBody.code === 'VF' ? ' .' :
            responseBody.code === 'VF' ? ' .' :
            responseBody.code === 'NT' ? '  .' : 
            responseBody.code === 'DBE' ? '  .' : '';
        const isSuccessed = responseBody !== null && responseBody?.code === 'SU'; 
        if(!isSuccessed) {
          alert(message)
          unShow();
          return;
        }

        const { name, purpose, count } = responseBody as GetToolResponseDto;
        setName(name);
        setPurpose(purpose);
        setCount(String(count));
    };

    // function: patch tool response   //
    const patchToolResponse = (responseBody: ResponseDto | null) => {
      const message =
            !responseBody ? '  .' :
            responseBody.code === 'VF' ? '  .' :
            responseBody.code === 'AF' ? ' .' :
            responseBody.code === 'NT' ? '  .' : 
            responseBody.code === 'DBE' ? '  .' : '';
        const isSuccessed = responseBody !== null && responseBody?.code === 'SU'; 
        if(!isSuccessed) {
          alert(message)
          return;
        };

        getToolLIst();
        unShow();
      }
    // event handler:       //
    const onNameChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
      const { value } = event.target;
      //      
      setName(value);
  };

  // event handler:      //
  const onPurposeChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
      const { value } = event.target;
      setPurpose(value);
  };

  // event handler:      //
  const onCountChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
      const { value } = event.target;
      //    
      const regexp = /^[0-9]*$/;
      //   
      const isNumber = regexp.test(value);
      if (!isNumber) return;
      setCount(value);
  };

    // event handler:       //
    const onUpdateButtonClickHandler = () => {
        if (!name || !purpose || !count ) {
          alert('  .')
          return;
        }

        const accessToken = cookies[ACCESS_TOKEN];
        if (!accessToken) return;

        const requestBody: PatchToolRequestDto = {
            name, purpose, count: Number(count)
        };
        patchToolRequest(requestBody, toolNumber, accessToken).then(patchToolResponse);
    };


    // effect: toolNumber     //
    useEffect(() => {
        const accessToken = cookies[ACCESS_TOKEN];
        if (!accessToken) return;
        getToolRequest(toolNumber, accessToken).then(getToolResponse)
    }, [toolNumber]);

    // render:     //
    return (
        <div className='post-patch-box'>
            <div className='post-patch-input-container'>
                <div className='input-box'>
                    <div className='input-label'></div>
                    <input className='input' value={name} placeholder=' ' onChange={onNameChangeHandler} />
                </div>
                <div className='input-box' style={{ flex: 1 }}>
                    <div className='input-label'></div>
                    <input className='input' value={purpose} placeholder=' ' onChange={onPurposeChangeHandler} />
                </div>
                <div className='input-box'>
                    <div className='input-label'></div>
                    <input className='input' value={count} placeholder=' ' onChange={onCountChangeHandler} />
                </div>
            </div>
            <div className='button second' onClick={onUpdateButtonClickHandler}></div>
            <div className='button disable' onClick={unShow}></div>
        </div>
    )

}

// interface:     Properties //
interface TableRowProps {
    tool: Tool;
    getToolList : () => void;
    onUpdateButtonClickHandler: (toolNumber: number) => void;
    
}

// component:     //
function TableRow({ tool, getToolList, onUpdateButtonClickHandler }: TableRowProps) {

    // state: cookie  //
    const [cookies] = useCookies();

    // function: delete tool response   //
    const deleteToolResponse = (responseBody: ResponseDto | null) => {
        const message =
            !responseBody ? '  .' :
            responseBody.code === 'VF' ? ' .' :
            responseBody.code === 'AF' ? ' .' :
            responseBody.code === 'NT' ? '  .' :
            responseBody.code === 'DBE' ? '  .' : '';

            const isSuccessed = responseBody !== null && responseBody?.code === 'SU'; 

        if (!isSuccessed) {
        alert(message)
        return;
      }

      getToolList();
    };

    // event handler:       //
    const onDelteButtonClickHandler = () => {
        const isConfirm = window.confirm(' ?');
        if (!isConfirm) return;

        const accessToken = cookies[ACCESS_TOKEN];
        if (!accessToken) return;
        deleteToolRequest(tool.toolNumber, accessToken).then(deleteToolResponse);
    };

    // render:      //
    return (
        <div className='tr'>
            <div className='td-tool-number'>{tool.toolNumber}</div>
            <div className='td-name'>{tool.name}</div>
            <div className='td-purpose'>{tool.purpose}</div>
            <div className='td-count'>{tool.count}</div>
            <div className='td-buttons'>
                <div className='td-edit'>
                    <div className='icon-button edit' onClick={() => onUpdateButtonClickHandler(tool.toolNumber)}></div>
                </div>
                <div className='td-delete'>
                    <div className='icon-button trash' onClick={onDelteButtonClickHandler}></div>
                </div>
            </div>
        </div>
    )

}

// component:     //
export default function MM() {

    // state: cookie  //
    const [cookies] = useCookies();

    // state:       //
    const [showPostBox, setShowPostBox] = useState<boolean>(false);
    const [showPatchBox, setShowPatchBox] = useState<boolean>(false);
    const [patchToolNumber, setPatchToolNumber] = useState<number>(0);

    // state:   //
    const [searchWord, setSearchWord] = useState<string>('');

    // state:    //
    const [originalList, setOriginalList] = useState<Tool[]>([]);

    const { 
        currentPage, totalPage, totalCount, viewList, pageList,
        setTotalList, initViewList,
        onPageClickHandler, onPreSectionClickHandler, onNextSectionClickHandler
    } = usePagenation<Tool>();


    // function: tool list   //
    const getToolList = () => {
      const accessToken = cookies[ACCESS_TOKEN];
      if (!accessToken) return;
      getToolListRequest(accessToken).then(getToolListResponse);
    };
    
    // function: get tool list response   //
    const getToolListResponse = (resposenBody: GetToolListResponseDto | ResponseDto | null) => {
        const message = 
            !resposenBody ? '  .' :
            resposenBody.code === 'AF' ? ' .' :
            resposenBody.code === 'DBE' ? '  .' : '';

        const isSuccessed = resposenBody !== null && resposenBody.code === 'SU';
        if (!isSuccessed) {
            alert(message);
            return;
        }

        const { tools } = resposenBody as GetToolListResponseDto;
        setTotalList(tools);
        setOriginalList(tools);
    };

    
    // // function: get tool list response   //

    // function:       //
    const unShowPostBox = () => setShowPostBox(false);

    // function:       //
    const unShowPatchBox = () => setShowPatchBox(false);

    // event handler:       //
    const onPostButtonClickHandler = () => {
        setShowPostBox(true);
        setShowPatchBox(false);
    };

    // event handler:       //
    const onUpdateButtonClickHandler = (toolNumber: number) => {
        setShowPatchBox(true);
        setPatchToolNumber(toolNumber);
    };

    // event handler:      //
    const onSearchWordChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        setSearchWord(value);
    };

    // event handler:       //
    const onSearchButtonClickHandler = () => {
        const searchedToolList = originalList.filter(tool => tool.name.includes(searchWord));
        setTotalList(searchedToolList);
        initViewList(searchedToolList);
        // initPageList(searchedToolList.length);
    };

    // effect:       //
    useEffect(getToolList, []);

    // render:      //
    return (
        <div id='mm-wrapper'>
            {/*       */}
            {showPostBox && <PostBox unShow={unShowPostBox} getToolList={getToolList} />}
            {showPatchBox && <PatchBox unShow={unShowPatchBox} toolNumber={patchToolNumber} getToolLIst={getToolList} />}
            <div className='top'>
                <div className='top-text'> <span className='emphasis'>{totalCount}</span> |  <span className='emphasis'>{currentPage}/{totalPage}</span></div>
                {!showPostBox && !showPatchBox && <div className='button primary' onClick={onPostButtonClickHandler}></div>}
            </div>
            <div className='main'>
                <div className='table'>
                    <div className='th'>
                        <div className='td-tool-number'></div>
                        <div className='td-name'></div>
                        <div className='td-purpose'></div>
                        <div className='td-count'></div>
                        <div className='td-buttons'>
                            <div className='td-edit'></div>
                            <div className='td-delete'></div>
                        </div>
                    </div>
                    {viewList.map((tool, index) => <TableRow key={index} tool={tool} onUpdateButtonClickHandler={onUpdateButtonClickHandler} getToolList={getToolList} />)}
                </div>
            </div>
            <div className='bottom'>
                <Pagenation 
                    pageList={pageList}
                    currentPage={currentPage}
                    onPageClickHandler={onPageClickHandler}
                    onPreSectionClickHandler={onPreSectionClickHandler}
                    onNextSectionClickHandler={onNextSectionClickHandler}
                />
                <div className='search-box'>
                    <input className='search-input' value={searchWord} placeholder=' .' onChange={onSearchWordChangeHandler} />
                    <div className='button disable' onClick={onSearchButtonClickHandler}></div>
                </div>
            </div>
        </div>
    )

}