import React, { ChangeEvent, useEffect, useState } from 'react'
import './style.css';
import { useNavigate, useParams } from 'react-router';
import { useCookies } from 'react-cookie';
import { ACCESS_TOKEN, CS_ABSOLUTE_PATH, CS_UPDATE_ABSOLUTE_PATH } from 'src/constants';
import { deleteCustomerRequest, getCareRecordListRequest, getCustomerRequest, getToolListRequest, postCareRecordRequest } from 'src/apis';
import { GetCareRecordResponseDto, GetCustomerResponseDto } from 'src/apis/dto/response/customer';
import { ResponseDto } from 'src/apis/dto/response';
import { useSignInUserStore } from 'src/stores';
import { usePagenation } from 'src/hooks';
import { CareRecord, Tool } from 'src/types';
import { GetToolListResponseDto } from 'src/apis/dto/response/tool';
import { count } from 'console';
import { PostCareRecordRequestDto } from 'src/apis/dto/request/customer';
import Pagenation from 'src/components/Pagenation';

// component:      //
export default function CSDetail() {

  // state:      //
  const { customerNumber } = useParams();

  // state:    //
  const { signInUser } = useSignInUserStore();

  // state: cookie  //
  const [cookies] = useCookies();

  // state:    //
  const [profileImage, setProfileImage] = useState<string>('');
  const [name, setName] = useState<string>('');
  const [birth, setBirth] = useState<string>('');
  const [charger, setCharger] = useState<string>('');
  const [chargerName, setChargerName] = useState<string>('');
  const [address, setAddress] = useState<string>('');

  // state:    //
  const {
      currentPage, totalPage, totalCount, viewList,
      setTotalList, initViewList, ...PagenationProps
  } = usePagenation<CareRecord>();

  // state:       //
  const [showSelector, setShowSelector] = useState<boolean>(false);

  // state:    //
  const [selectedTool, setSelectedTool] = useState<Tool | null>(null);
  // state:     //
  const [recordContents, setRecordContents] = useState<string>('');
  // state:     //
  const [usedToolCount, setUsedToolCount] = useState<string>('');

  // state:      //
  const [toolList, setToolList] = useState<Tool[]>([]);

  // variable:   //
  const isChager = charger === signInUser?.userId;

  // function:   //
  const navigator = useNavigate();

  // function:     //
  const changeDateFormat = (date: string) => {
      const yy = date.substring(2, 4);
      const mm = date.substring(5, 7);
      const dd = date.substring(8, 10);
      return `${yy}.${mm}.${dd}`;
  };

  // function: get customer response   //
  const getCustomerResponse = (responseBody: GetCustomerResponseDto | ResponseDto | null) => {
    const message =
        !responseBody ? '  .' :
        responseBody.code === 'VF' ? ' .' :
        responseBody.code === 'AF' ? ' .' :
        responseBody.code === 'NC' ? '  .' :
        responseBody.code === 'DBE' ? '  .' : '';

        const isSuccessed = responseBody !== null && responseBody.code === 'SU';
        if (!isSuccessed) {
          alert(message);
          // issuccessed   navigator     .
          navigator(CS_ABSOLUTE_PATH);
          return;
        }

        const { profileImage, name, birth, chargerId, chargerName, address } = responseBody as GetCustomerResponseDto;
        setProfileImage(profileImage);
        setName(name);
        setBirth(birth);
        setCharger(chargerId);
        setChargerName(chargerName);
        setAddress(address);
  };
  
  // function: get care record list response   //
  const getCareRecordListResponse = (responseBody: GetCareRecordResponseDto | ResponseDto | null) => {
      const message =
          !responseBody ? '  .' :
          responseBody.code === 'VF' ? ' .' :
          responseBody.code === 'AF' ? ' .' :
          responseBody.code === 'DBE' ? '  .' : '';
      
      const isSuccessed = responseBody !== null && responseBody.code === 'SU';
      if (!isSuccessed) {
        alert(message);
        return;
      }

      const { careRecords } = responseBody as GetCareRecordResponseDto;
      setTotalList(careRecords);
  }; 

  // function: get tool list response   //
  const getToolListResponse = (responseBody: GetToolListResponseDto | ResponseDto | null) => {
      const message =
          !responseBody ? '  .' :
          responseBody.code === 'AF' ? ' .' :
          responseBody.code === 'DBE' ? '  .' : '';

      const isSuccessed = responseBody !== null && responseBody.code === 'SU';
      if (!isSuccessed) {
        alert(message);
        return;
      }

      const { tools } = responseBody as GetToolListResponseDto;
      const toolList = tools.filter(tool => tool.count > 0);
      setToolList(toolList);

  };

  // function: post care record response   //
  const postCareRecordResponse = (responseBody: ResponseDto | null) => {
    const message = 
        !responseBody ? '  .' : 
        responseBody.code === 'VF' ? '  .' : 
        responseBody.code === 'AF' ? ' .' : 
        responseBody.code === 'NP' ? ' .' :
        responseBody.code === 'TI' ? '  .' :
        responseBody.code === 'DBE' ? '  .' : '';
    
    const isSuccessed = responseBody !== null && responseBody.code === 'SU';
    if (!isSuccessed) {
        alert(message);
        return;
    }

    if (!customerNumber) return;
    const accessToken = cookies[ACCESS_TOKEN];
    if (!accessToken) return;

    getCareRecordListRequest(customerNumber, accessToken).then(getCareRecordListResponse);
};

  // function: delete customer response   //
  const deleteCustomerResponse = (responseBody: ResponseDto | null) => {
      const message =
          !responseBody ? '  .' :
          responseBody.code === 'VF' ? ' .' :
          responseBody.code === 'AF' ? ' .' :
          responseBody.code === 'NC' ? '  .' :
          responseBody.code === 'NP' ? ' .' :
          responseBody.code === 'DBE' ? '  .' : '';
          
          const isSuccessed = responseBody !== null && responseBody.code === 'SU';
          if (!isSuccessed) {
            alert(message);
            return;
          }
          //     PATH  .
          navigator(CS_ABSOLUTE_PATH);
  }

  // event handler:       //
  const onRecordConttentsChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
      const { value } = event.target;
      setRecordContents(value);
    };
    
    // event handler:       //
    const onUsedToolCountChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        if (!selectedTool) return;
      
        const { value } = event.target;
        //     value 
        const regexp = /^\d*$/;
        const isMatched = regexp.test(value);
        if (!isMatched) return;

        setUsedToolCount(value);
  };

  // event handler:     //
  const onSelectorClickHandler = () => {
      setShowSelector(!showSelector);
  };

  // event handler:     //
  const onToolSelectHandler = (tool: Tool | null) => {
      setSelectedTool(tool);
      if (!tool) setUsedToolCount('');
      setShowSelector(false);
  };

  // event handler:       //
  const onRecordButtonClickHandler = () => {
    if (!isChager) {
        alert(' .');
        return;
    }

    if (!recordContents) {
        alert(' .');
        return;
    }

    if (selectedTool && !usedToolCount) {
        alert(' .');
        return;
    }

    if (!customerNumber) return;

    const accessToken = cookies[ACCESS_TOKEN];
    if (!accessToken) return;

    const requestBody: PostCareRecordRequestDto = {
        contents: recordContents,
        usedToolNumber: selectedTool ? selectedTool.toolNumber : null,
        count: selectedTool ? Number(usedToolCount) : null
    };

    postCareRecordRequest(requestBody, customerNumber, accessToken).then(postCareRecordResponse);
};

  // event handler:      //
  const onListButtonClickHandler = () => {
    navigator(CS_ABSOLUTE_PATH);
  ;}

  // event handler:      //
  const onUpdateButtonClickHandler = () => {
    if (!isChager) return;
    if (!customerNumber) return;
    navigator(CS_UPDATE_ABSOLUTE_PATH(customerNumber));
  };
  
  // event handler:      //
  const ondeleteButtonClickHandler = () => {
      if (!isChager) return;
    
      const isConfirm = window.confirm(' ?');
      if (!isConfirm) return;

      if (!customerNumber) return;

      const accessToken = cookies[ACCESS_TOKEN];
      if (!accessToken) return;

      deleteCustomerRequest(customerNumber, accessToken).then(deleteCustomerResponse);
  };

  // effect:         //
  useEffect(() => {
    if (!customerNumber) return;
    const accessToken = cookies[ACCESS_TOKEN];
    if (!accessToken) return;

    getCustomerRequest(customerNumber, accessToken).then(getCustomerResponse);
    getCareRecordListRequest(customerNumber, accessToken).then(getCareRecordListResponse);
    getToolListRequest(accessToken).then(getToolListResponse);
  }, [customerNumber]);

  // render:       //
  return (
    <div id='cs-detail-wrapper'>
      <div className='top'>   {/* backgroundimage     ``  */}
          <div className='profile-image' style={{backgroundImage: `url(${profileImage})` }}></div>
          <div className='info-box'>
            <div className='info-item'>
              <div className='info-label'> </div>
              <div className='info-text'>{name}</div>
            </div>
            <div className='info-item'>
              <div className='info-label'></div>
              <div className='info-text'>{birth}</div>
            </div>
            <div className='info-item'>
              <div className='info-label'></div>
              <div className='info-text'>{charger}</div>
            </div>
            <div className='info-item'>
              <div className='info-label'></div>
              <div className='info-text'>{address}</div>
            </div>
          </div>
      </div>
      <div className='middle'>
        <div className='title'> </div>
        <div className='main'>
        </div>
          <div className='main-bottom'>
            <Pagenation currentPage={currentPage} {...PagenationProps}/>
          </div>
          <div className='table'>
            <div className='th'>
              <div className='td-record-date'></div>
              <div className='td-record-contents'></div>
              <div className='td-used-tool'> </div>
              <div className='td-used-tool-count'></div>
            </div>
            {viewList.map((careRecord, index) =>
            <div key={index} className='tr'>
              <div className='td-record-date'>{changeDateFormat(careRecord.recordDate)}</div>
              <div className='td-record-contents'>{careRecord.contents}</div>
              <div className='td-used-tool'>{careRecord.usedToolName}</div>
              <div className='td-used-tool-count'>{careRecord.count}</div>
            </div>
            )}
          
        </div>
      </div>
      {isChager &&
      <div className='middle'>
        <div className='title'> </div>
        <div className='record-write-box'>
            <div className='record-write-content-box'>
              <div className='input-box' style={{flex: 1}}>
                <div className='label'></div>
                <input value={recordContents} placeholder=' .' onChange={onRecordConttentsChangeHandler}/>
              </div>
              <div className='button disable' onClick={onRecordButtonClickHandler}></div>
            </div>
            <div className='record-write-tool-box'>
              <div className='input-box'>
                <div className='label'></div>
                
                {showSelector ?
                <div className='selector open'>
                  <div className='selected-item'>{selectedTool ? selectedTool.name : ''}</div>
                  <div className='arrow-up-button' onClick={onSelectorClickHandler}></div>
                  <div className='selector-box'>
                      {toolList.map((tool, index) =>  <div className='selector-option' onClick={() => onToolSelectHandler(null)}> </div>   
                      )}
                      {toolList.map((tool, index) =>  <div className='selector-option' key={index} onClick={() => onToolSelectHandler(tool)}>{tool.name}</div>   
                      )}
                  </div>
                </div>
                :
                <div className='selector close'>
                  <div className='selected-item'>{selectedTool ? selectedTool.name : ''}</div>
                  <div className='arrow-down-button' onClick={onSelectorClickHandler}></div>
                </div>
                }
              </div>
              <div className='input-box'>
                <div className='label'></div>
                <input value={usedToolCount} placeholder=' .' onChange={onUsedToolCountChangeHandler}/>
              </div>

            </div>
        </div>
      </div>
      }
      <div className='bottom'>
        <div className='button primary' onClick={onListButtonClickHandler}></div>
        {isChager &&
        <div className='button-box'>
          <div className='button second' onClick={onUpdateButtonClickHandler}></div>
          <div className='button error' onClick={ondeleteButtonClickHandler}></div>
        </div>
        }
      </div>
    </div>
  )
}
