import React, { ChangeEvent, KeyboardEvent, useEffect, useRef, useState } from 'react'
import './style.css';
import { Address, useDaumPostcodePopup } from 'react-daum-postcode';
import { useSignInUserStore } from 'src/stores';
import { usePagenation } from 'src/hooks';
import Pagenation from 'src/components/Pagenation';
import { useCookies } from 'react-cookie';
import { ACCESS_TOKEN, CS_ABSOLUTE_PATH } from 'src/constants';
import { fileUploadRequest, getNurseListRequest, postCustomerRequest } from 'src/apis';
import { GetNurseListResponseDto } from 'src/apis/dto/response/nurse';
import { ResponseDto } from 'src/apis/dto/response';
import { Nurse } from 'src/types';
import { useNavigate } from 'react-router';
import { PostCustomerRequestDto } from 'src/apis/dto/request/customer';

// variable:    URL //
const defaultProfileImageUrl = 'https://blog.kakaocdn.net/dn/4CElL/btrQw18lZMc/Q0oOxqQNdL6kZp0iSKLbV1/img.png'


// component:      //
export default function CSWrite() {

  // state:    //
  const { signInUser } = useSignInUserStore();

  // state:    //
  const imageInputRef = useRef<HTMLInputElement | null>(null);

  // state: cookie  //
  const [cookies] = useCookies();

  // state:   URL  //
  const [previewUrl, setPreviewUrl] = useState<string>(defaultProfileImageUrl);

  // state:    //
  const [modalOpen, setModalOpen] = useState<boolean>(false);

  // state:    //
  const [name, setName] = useState<string>('');
  const [birth, setBirth] = useState<string>('');
  const [profileImageFile, setProfileImageFile] = useState<File|null>(null);
  const [charger, setCharger] = useState<string>('');
  const [chargerName, setChargerName] = useState<string>('');
  const [address, setAddress] = useState<string>('');
  const [location, setLocation] = useState<string>('');

  // state:   //
  const [searchWord, setSearchWord] = useState<string>('');

  // state:    //
  const [originalList, setOriginalList] = useState<Nurse[]>([]);

  // state:    //
  const {
      currentPage, totalPage, totalCount, viewList,
      setTotalList, initViewList, ...PagenationProps
  } = usePagenation<Nurse>();

  // function:   //
  const navigator = useNavigate();

  // function:      //
  const daumPostcodePopup = useDaumPostcodePopup();

  // function:       //
  const daumPostcodeComplete = (result:Address) => {
      const {address, sigungu } = result;
      setAddress(address);
      setLocation(sigungu);
      console.log(sigungu);
  };

  // function: get nurse list response   //
  const getNurseListResponse = (responseBody: GetNurseListResponseDto | ResponseDto | null) => {
    const message =
      !responseBody ? '  .' :
      responseBody.code === 'AF' ? ' .' :
      responseBody.code === 'DBE' ? '  .' : '';

      const isSuccessed = responseBody !== null && responseBody.code === 'SU';
      if (!isSuccessed) {
        alert(message);
        return;
      }

      const { nurses } = responseBody as GetNurseListResponseDto;
      setTotalList(nurses);
      setOriginalList(nurses);
  }

  // function: post customer response   //
  const PostCustomerResponse = (responseBody: ResponseDto | null) => {
      const message =
          !responseBody ? '  .' :
          responseBody.code === 'VF' ? '  .' :
          responseBody.code === 'AF' ? ' .' :
          responseBody.code === 'NI' ? '  .' :
          responseBody.code === 'DBE' ? '  .' : '';

      const isSuccessed = responseBody !== null && responseBody.code === 'SU';
      if (!isSuccessed) {
        alert(message)
        return;
      }

      navigator(CS_ABSOLUTE_PATH);
  };

  // event handler:      //
  const onProfileImageClickHandler = () => {
    const { current } = imageInputRef;
    if (!current) return;
    current.click();
  };

  // event handler:      //
  const onImageInputChangeHandler = (event:ChangeEvent<HTMLInputElement>) => {
      const { files } = event.target;

      if (!files || !files.length) return;

      const file = files[0];
      setProfileImageFile(file);

      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onloadend = () => {
          setPreviewUrl(fileReader.result as string);
      };
  };

  // event handler:      //
  const onNameChangeHandler = (event:ChangeEvent<HTMLInputElement>) => {
      const { value } = event.target;
      setName(value);
  };

  // event handler:      //
  const onBirthChangeHandler = (event:ChangeEvent<HTMLInputElement>) => {
      const { value } = event.target;
      const regexp = /^\d{0,6}$/;
      const isMatched = regexp.test(value);
      if (!isMatched) return;
      setBirth(value);
  };

  // event handler:       //
  const onAddressButtonClickHandler = () => {
      daumPostcodePopup({ onComplete: daumPostcodeComplete });
  };

  // event handler:      //
  const onChargerSelfButtonClickHandler = () => {
      if (!signInUser) return;
      setCharger(signInUser.userId);
      setChargerName(signInUser.name);
  };

  // event handler:     //
  const onModalOpenHandler = () => {
      setModalOpen(!modalOpen);
  };

  // event handler:     //
  const onSearchWordChangeHandler = (event:ChangeEvent<HTMLInputElement>) => {
      const { value } = event.target;
      setSearchWord(value);
  };

  // event handler:     //
  const onSearchWordKeydownHandler = (event:KeyboardEvent<HTMLInputElement>) => {
      const { key } = event;
      if (key === 'Enter') onSearchButtonClickHandler(); 
  };

  // event handler:      //
  const onSearchButtonClickHandler = () => {
      const searchedNurseList = originalList.filter(nurse => nurse.name.includes(searchWord));
      setTotalList(searchedNurseList);
      initViewList(searchedNurseList);
      console.log(searchedNurseList);
  };

  // event handler:     //
  const onNurseSelectHandler = (nurse: Nurse) => {
      setCharger(nurse.nurseId);
      setChargerName(nurse.name);
      setModalOpen(false);
  };

  // event handler:      //
  const onListButtonClickHandler = () => {
      navigator(CS_ABSOLUTE_PATH);
  };

  // event handler:      //
  const onPostClickHandler = async () => {
      if (!name || !birth || !charger || !address || !location) return;

      const accessToken = cookies[ACCESS_TOKEN];
      if (!accessToken) return;

      let url: string | null = defaultProfileImageUrl;
      if (profileImageFile) {
          const formData = new FormData();
          formData.append('file', profileImageFile);
          url = await fileUploadRequest(formData);
      }
      url = url ? url : defaultProfileImageUrl;

      const requestBody: PostCustomerRequestDto = {
          profileImage: url,
          name,
          birth,
          charger,
          address,
          location
      };
      postCustomerRequest(requestBody, accessToken).then(PostCustomerResponse);  
    };

  // effect:        //
  useEffect(() => {
    const accessToken = cookies[ACCESS_TOKEN];
    if (!accessToken) return;
    getNurseListRequest(accessToken).then(getNurseListResponse);
  }, []);

  // effect:         //
  useEffect(() => {
    document.body.style.overflow = modalOpen ? 'hidden' : 'auto';
    return () => {
      document.body.style.overflow = 'auto';
    } 
  }, [modalOpen]);

  // render:       //
  return (
    <div id='cs-write-wrapper'>
      <div className='main'>
        <div className='profile-image' style={{ backgroundImage: `url(${previewUrl})` }} onClick={onProfileImageClickHandler}>
          <input ref={imageInputRef} style={{display:'none'}} type='file' accept='image/*' onChange={onImageInputChangeHandler} />
        </div>
        <div className='input-container'>
          <div className='input-box'>
            <div className='input-label'> </div>
            <input className='input' value={name} placeholder='  .' onChange={onNameChangeHandler} />
          </div>
          <div className='input-box'>
            <div className='input-label'></div>
            <input className='input' value={birth} placeholder='6  .' onChange={onBirthChangeHandler}/>
          </div>
          <div className='input-box'>
            <div className='input-label'></div>
            <input className='input' value={chargerName} readOnly placeholder=' .' />
            <div className='button-box'>
              <div className='button second' onClick={onChargerSelfButtonClickHandler}></div>
              <div className='button disable' onClick={onModalOpenHandler} onKeyDown={onSearchWordKeydownHandler}></div>
            </div>
          </div>
          <div className='input-box'>
            <div className='input-label'></div>
            <input className='input' value={address} placeholder=' .' />
            <div className='button-box'>
              <div className='button disable' onClick={onAddressButtonClickHandler}></div>
            </div>
          </div>
        </div>
      </div>
      <div className='bottom'>
        <div className='button primary' onClick={onListButtonClickHandler}></div>
        <div className='button second' onClick={onPostClickHandler}></div>
      </div>
      {modalOpen &&
      <div className='modal'>
          <div className='modal-box'>
            <div className='modal-top'>
              <div className='modal-label'> </div>
              <div className='modal-input-box'>
                <input className='modal-input' value={searchWord} placeholder=' .' onChange={onSearchWordChangeHandler} onKeyDown={onSearchWordKeydownHandler}/>
                <div className='button disable'onClick={onSearchButtonClickHandler} ></div>
              </div>
            </div>
            <div className='modal-main'>
              <div className='table'>
                <div className='th'>
                    <div className='td-nurse-id'>ID</div>
                    <div className='td-nurse-name'></div>
                    <div className='td-nurse-tel-number'></div>
                </div>
                {/*viewList  map nurse  .*/}
                {viewList.map((nurse, index) =>
                <div key={index} className='tr' onClick={() => onNurseSelectHandler(nurse)}>
                    <div className='td-nurse-id'>{nurse.nurseId}</div>
                    <div className='td-nurse-name'>{nurse.name}</div>
                    <div className='td-nurse-tel-number'>{nurse.telNumber}</div>
                </div>
                )}
              </div>
              <div className='modal-pagenation-box'>
              <Pagenation currentPage={currentPage} {...PagenationProps} />
              </div>
            </div>
            <div className='modal-bottom'>
              <div className='button disable'  onClick={onModalOpenHandler}></div>
            </div>
          </div>
      </div>
  }
    </div>
  )
}
