import React, { useEffect } from 'react'
import { Outlet, useLocation, useNavigate } from 'react-router'
import './style.css';
import { useCookies } from 'react-cookie';
import { ACCESS_TOKEN, AUTH_ABSOLUTE_PATH, CS_ABSOLUTE_PATH, CS_PATH, HR_ABSOLUTE_PATH, HR_PATH, MM_ABSOLUTE_PATH, MM_PATH, ROOT_ABSOLUTE_PATH } from 'src/constants';
import path from 'path';

// component:   //
function Logo() {

  // render:    //
  return (
    <div id='layout-logo'>
      <div className='box'>
        <div className='title'></div>
        <div className='icon'></div>
      </div>
    </div>
  )
}

// component:   //
function Top() {
  
  // state: path  //
  const { pathname } = useLocation();
  // state: cookie  //
  const [cookies, setCookie, removeCookie] = useCookies();

  
  // variable:   //
  const path = 
  pathname.startsWith('/cs') ? ' ' : 
  pathname.startsWith('/mm') ? ' ' : 
  pathname.startsWith('/hr') ? ' ' : ''; 

  // function:   //
  //   useHook
  const navigator = useNavigate();

  // event handler:      //
  const onLogoutButtonClickHandler = () => {
    removeCookie('accessToken', { path: ROOT_ABSOLUTE_PATH });
    navigator(AUTH_ABSOLUTE_PATH);
  };
  
  // render:   //
  return (
    <div id='layout-top'>
      <div className='path'>{path}</div>
      <div className='button second' onClick={onLogoutButtonClickHandler}></div>
    </div>
  )
}

// component:    /
function SideNavigation() {

    // state: path  //
    //  use 
    const { pathname } = useLocation();

    // variable:     //
    const isCs = pathname.startsWith(CS_PATH);
    const isMm = pathname.startsWith(MM_PATH);
    const isHr = pathname.startsWith(HR_PATH);

    // function:   //
    const navigator = useNavigate();

    // event handler:      //
    const onItemClickHandler = (path: string) => {
        navigator(path);
    };

    // render:    //
    return (
      <div id='layout-side-navigation'>
        <div className='navigation'>
          <div className={`navigation-item ${isCs ? 'active' : ''}`} onClick={() => onItemClickHandler(CS_ABSOLUTE_PATH)}>
            <div className={`icon ${isCs ? 'cs-active-icon' : 'cs-icon'}`}></div>
            <div className='item-text'> </div>
          </div>
          <div className={`navigation-item ${isMm ? 'active' : ''}`} onClick={() => onItemClickHandler(MM_ABSOLUTE_PATH)}>
            <div className={`icon ${isMm ? 'mm-active-icon' : 'mm-icon'}`}></div>
            <div className='item-text'> </div>
          </div>
          <div className={`navigation-item ${isHr ? 'active' : ''}`} onClick={() => onItemClickHandler(HR_ABSOLUTE_PATH)}>
            <div className={`icon ${isHr ? 'hr-active-icon' : 'hr-icon'}`}></div>
            <div className='item-text'> </div>
          </div>
        </div>
      </div>
    );
}

// component:    //
export default function MainLayout() {

  // state:   //
  const [cookies] = useCookies();
  
  // function :   //
  const navigator = useNavigate();
  
  // effect:      //
  useEffect(() => {
      // if (!cookies[ACCESS_TOKEN]) navigator(AUTH_ABSOLUTE_PATH);
  }, []);

  // render:     //
  return (
    <div id='main-layout'>
        <Logo />
        <Top />
        <SideNavigation />
        <div id='main-wrapper'>
          <Outlet />
        </div>
    </div>
  )
}
