import React, { ChangeEvent, KeyboardEvent, MouseEvent, useEffect, useState } from 'react'
import './style.css'
import { usePagenation } from 'src/hooks'
import { Nurse } from 'src/types'
import { useCookies } from 'react-cookie'
import { ACCESS_TOKEN, HR_DETAIL_ABSOLUTE_PATH } from 'src/constants'
import { getNurseListRequest } from 'src/apis'
import { GetNurseListResponseDto } from 'src/apis/dto/response/nurse'
import { ResponseDto } from 'src/apis/dto/response'
import Pagenation from 'src/components/Pagenation'
import { useHrDetailUpdateStore, useSignInUserStore } from 'src/stores'
import { useNavigate } from 'react-router'

// interface:     Properties //
interface TableRowProps {
  nurse: Nurse;
}

// component:     //
function TableRow({nurse}: TableRowProps) {

    // state:    //
    const { signInUser } = useSignInUserStore();

    // state:     //
    const { setUpdate } = useHrDetailUpdateStore();

    // variable:   //
    const isSignInUser = nurse.nurseId === signInUser?.userId;

    // function:   //
    const navigator = useNavigate();

    // event handler:     //
    const onRowClickHandler = (event:MouseEvent<HTMLDivElement>) => {
      event.stopPropagation();
        setUpdate(false);
        navigator(HR_DETAIL_ABSOLUTE_PATH(nurse.nurseId));
      };
      
      // event handler:      //
      const onUpdateButtonClickHandler = (event:MouseEvent<HTMLDivElement>) => {
      event.stopPropagation();
      setUpdate(true);
      navigator(HR_DETAIL_ABSOLUTE_PATH(nurse.nurseId));
    }

    // render:      //
    return (
      <div className='tr' onClick={onRowClickHandler}>
      <div className='td-nurse-infos'>
        <div className='td-nurse-id'>{nurse.nurseId}</div>
        <div className='td-nurse-name'>{nurse.name}</div>
        <div className='td-nurse-tel'>{nurse.telNumber}</div>
      </div>
      {isSignInUser &&
      <div className='td-buttons'>
          <div className='td-edit'>
            <div className='icon-button edit' onClick={onUpdateButtonClickHandler}></div>
          </div>
      </div>
      }
    </div>
    )
}

// component:     //
export default function HR() {
  // state: cookie  //
  const [cookies] = useCookies();

  // state:    //
  const [originalList, setOriginalList] = useState<Nurse[]>([]);

  // state:   //
  const [searchWord, setSearchWord] = useState<string>('');

  // state:    //
  const {
      currentPage, totalPage, totalCount, viewList,
      setTotalList, initViewList, ...pagenationProps
  } = usePagenation<Nurse>();

  // function: get nurse list response   //
  const getNurseListResponse = (responseBody: GetNurseListResponseDto | ResponseDto |null) => {
      const message =
        !responseBody ? '  .' :
        responseBody.code === 'AF' ? ' .' :
        responseBody.code === 'DBE' ? '  .' : '';

      const isSuccessed = responseBody !== null && responseBody.code === 'SU';
      if (!isSuccessed) {
        alert(message);
        return;
      }
      
      // nurses       
      const { nurses } = responseBody as GetNurseListResponseDto;
      setTotalList(nurses);
      setOriginalList(nurses);
  };

  // event handler:     //
  const onSearchWordChangeHandler = (event:ChangeEvent<HTMLInputElement>) => {
      const { value } = event.target;
      setSearchWord(value);
  };

  // event handler:      //
  const onSearchButtonClickHandler = () => {
      const searchedNurseList = originalList.filter(nurse => nurse.name.includes(searchWord));
      setTotalList(searchedNurseList);
      initViewList(searchedNurseList);
  };

  

  // event handler:       //
  const onkeyDownHandler = (event:KeyboardEvent<HTMLInputElement>) => {
    const {key}  = event;
    if (key === 'Enter') onSearchButtonClickHandler();
  }

  // effect:       //
  useEffect(() => {
      const accessToken = cookies[ACCESS_TOKEN];
      if (!accessToken) return;

      getNurseListRequest(accessToken).then(getNurseListResponse);
  }, []);

  // render:      //  
  return (
    <div id='hr-wrapper'>
        <div className='top'>
          <div className='top-text'>
               <span className='emphasis'>{totalCount}</span> |  <span className='emphasis'>{currentPage}/{totalPage}</span>
          </div>
        </div>
        <div className='main'>
            <div className='table'>
                <div className='th'>
                  <div className='td-nurse-infos'>
                    <div className='td-nurse-id'> ID</div>
                    <div className='td-nurse-name'> </div>
                    <div className='td-nurse-tel'></div>
                  </div>
                  { }
                  <div className='td-buttons'>
                      <div className='td-edit'></div>
                  </div>
                </div>
                {viewList.map((nurse, index) => <TableRow key={index} nurse={nurse}/>)}
            </div>
        </div>
        <div className='bottom'>
          <Pagenation currentPage={currentPage} {...pagenationProps}/>
          <div className='search-box'>
              <input className='search-input' value={searchWord} placeholder=' .' onChange={onSearchWordChangeHandler} onKeyDown={onkeyDownHandler}/>
              <div className='button disable' onClick={onSearchButtonClickHandler}></div>
          </div>
        </div>
    </div>
  )
}
